<template>
  <div>
    <div class="nav-wrapper">
      <form>
        <div class="input-field">
          <input
            id="search"
            v-model="searchDesc"
            placeholder="Szukaj opisu"
            type="search"
            required>
          <label class="label-icon" for="search"><i class="material-icons">search</i></label>
          <i
            @click="searchDesc = ''"
            class="material-icons">
            close</i>
        </div>
      </form>
    </div>
    <add-t
      :addTData="addTData"
      :onAdd="onAdd"
    />
    <list-t
      :editId="editId"
      :editData="editData"
      :onCancel="onCancel"
      :onDelete="onDelete"
      :onEdit="onEdit"
      :onEditSubmit="onEditSubmit"
      :sortedT="sortedT"
      :loading="loading"
    />
  </div>
</template>

<script>
import db from '@/db'
import AddT from './AddT'
import ListT from './ListT'

export default {
  name: 'Territories',
  data () {
    return {
      loading: true,
      territories: [],
      addTData: {
        tid: '',
        number: '',
        desc: '',
        tel: false,
        free: true,
        given: [],
        recieved: []
      },
      editId: '',
      editData: {},
      searchDesc: ''
    }
  },
  components: {
    AddT,
    ListT
  },
  computed: {
    sortedT () {
      return this.territories.slice().filter(t=>t.desc.includes(this.searchDesc)).sort((a, b) => {
        return a.number - b.number
      })
    }
    },
    created () {
      this.getT()
    },
  methods: {
    getT () {
      db.collection('territories').get().then(querySnapshot => {
        const territories = []
        querySnapshot.forEach(doc => {
          territories.push(doc.data())
        })
        this.territories = territories
        this.loading = false
      })
    },
    onAdd () {
      this.addTData.tid = new Date().getTime()
      db.collection('territories').add(this.addTData).then(this.getT)
      this.addTData = {
        tid: '',
        number: '',
        desc: '',
        tel: false,
        free: true,
        given: [],
        recieved: []
      }
    },
    onDelete (tid) {
      db.collection('territories')
        .where('tid', '==', tid).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.delete().then(this.getT)
          })
        })
    },
    onEdit (t) {
      this.editId = t.tid
      this.editData.number = t.number
      this.editData.desc = t.desc
      this.editData.tel = t.tel
      this.editData.free = t.free
      this.editData.given = t.given
      this.editData.recieved = t.recieved
    },
    onCancel () {
      this.editId = ''
      this.editData.number = {}
    },
    onEditSubmit () {
      db.collection('territories')
        .where('tid', '==', this.editId).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.set({
              tid: this.editId,
              number: this.editData.number,
              desc: this.editData.desc,
              tel: this.editData.tel,
              free: this.editData.free,
              given: this.editData.given,
              recieved: this.editData.recieved,
            }).then(this.getT)
          })
          this.onCancel()
        })
    }
  }
}
</script>

<style scoped>

</style>



