<template>
  <div>
    <h3>Tereny</h3>
    <div
      v-if="loading"
      class="progress grey lighten-5">
      <div class="indeterminate green"></div>
    </div>
    <div
      v-for="t in sortedT"
      :key="t.tid"
      class="collection"
    >
      <div
        v-if="editId === t.tid"
        class="collection-item list-item"
        :class="{ 'green lighten-5': editId === t.tid }"
      >
        <div>
          <label>
            <input
              type="checkbox"
              class="filled-in"
              v-model="editData.tel"
              />
            <span>Telefoniczny</span>
          </label>
        </div>
        <div>
          <input
            class="validate"
            v-model="editData.desc"
          />
        </div>
        <div>
          <button
            class="btn-flat btn-large"
            @click="onEditSubmit">
            <i
              class="material-icons"
            >
              check
            </i>
          </button>
          <button
            class="btn-flat btn-large"
            @click="onCancel">
            <i
              class="material-icons"
            >
              cancel
            </i>
          </button>
        </div>
      </div>
      <div
        v-else
        class="collection-item list-item"
      >
        <div>
          <span>
            {{t.number}}
          </span>
          <span>
            {{ (t.tel)? "☎":"" }}
          </span>
        </div>
        <div>{{t.desc}}</div>
        <div>
          <button
            class="btn-flat btn"
            @click="onEdit(t)">
            <i
              class="material-icons"
            >
              mode_edit
            </i>
          </button>
            <button
            class="btn-flat btn"
            @click="onDelete(t.tid)">
            <i
              class="material-icons"
            >
              delete
            </i>
            </button>
            <router-link
              :to="{
                name: 'TPage',
                params: { tid: t.tid }
              }"
            >
              <button
                class="btn-flat btn">
                <i
                  class="material-icons"
                >
                  search
                </i>
              </button>
            </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ListT',
  props: [
    'editId',
    'editData',
    'onCancel',
    'onDelete',
    'onEdit',
    'onEditSubmit',
    'sortedT',
    'loading',
  ]
}
</script>

<style scoped>

</style>



