<template>
  <div>
    <h3>Głosiciele</h3>
    <div
      v-if="loading"
      class="progress grey lighten-5">
      <div class="indeterminate green"></div>
    </div>
    <div
      v-for="p in sortedP"
      :key="p.pid"
      class="collection"
    >
      <div
        v-if="editId === p.pid"
        class="collection-item list-item"
        :class="{ 'green lighten-5': editId === p.pid }"
      >
        <div>
          <input
            class="validate"
            v-model="editPData.name"
          />
        </div>
        <div>
          <input
            class="validate"
            v-model="editPData.surname"
          />
        </div>
        <div>
          <button
            class="btn-flat btn-large"
            @click="onEditSubmit">
            <i
              class="material-icons"
            >
              check
            </i>
          </button>
          <button
            class="btn-flat btn-large"
            @click="onCancel">
            <i
              class="material-icons"
            >
              cancel
            </i>
          </button>
        </div>
      </div>
      <div
        v-else
        class="collection-item list-item"
      >
        <div>{{p.name}}</div>
        <div>{{p.surname}}</div>
        <div>
          <button
            class="btn-flat btn"
            @click="onEdit(p)">
            <i
              class="material-icons"
            >
              mode_edit
            </i>
          </button>
            <button
            class="btn-flat btn"
            @click="onDelete(p.pid)">
            <i
              class="material-icons"
            >
              delete
            </i>
            </button>
            <router-link
              :to="{
                name: 'PPage',
                params: { pid: p.pid }
              }"
            >
              <button
                class="btn-flat btn">
                <i
                  class="material-icons"
                >
                  search
                </i>
              </button>
            </router-link>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ListP',
  props: [
    'editId',
    'editPData',
    'onCancel',
    'onDelete',
    'onEdit',
    'onEditSubmit',
    'sortedP',
    'loading',
  ]
}
</script>

<style scoped>

</style>



