<template>
  <div>
    <div v-if="loaded">
      <h2>
        {{number}}
      </h2>
      <h3>
        {{desc}}
      </h3>
      <p>ID: {{tid}}</p>
      <p>Tel: {{tel}}</p>
      <p>Free: {{free}}</p>
      <p>Given: {{given}}</p>
      <p>Recieved: {{recieved}}</p>
      <router-link :to="'/Territories'">
        <button class="waves-effect waves-light btn-small green green">Wróć</button>
      </router-link>
    </div>
    <div v-else class="progress grey lighten-5">
      <div class="indeterminate green"></div>
    </div>
  </div>
</template>

<script>
import db from '@/db'

export default {
  name: 'TPage',
  data () {
    return {
      tid: "",
      number: "",
      desc: "",
      tel: "",
      free: "",
      given: [],
      recieved: [],
      loaded: false
    }
  },
  beforeCreate () {
    db.collection('territories')
      .where('tid', '==', this.$route.params.tid).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const { tid, number, desc, tel, free, given, recieved } = doc.data()
          this.tid = tid
          this.number = number
          this.desc = desc
          this.tel = tel
          this.free = free
          this.given = given
          this.recieved = recieved
        })
        this.loaded = true
      })
  }
}
</script>

<style scoped>

</style>



