<template>
  <div>
    <h3>Dodaj teren</h3>

    <div class="card addP">
      <div>
        <input
          type="number"
          v-model="addTData.number"
          class="validate"
          placeholder="Numer terenu"
        >
      </div>
      <div>
        <input
          v-model="addTData.desc"
          class="validate"
          placeholder="Opis terenu"
        >
      </div>
      <div>
        <label>
          <input
            type="checkbox"
            class="filled-in"
            v-model="addTData.tel"
          />
          <span>Telefoniczny</span>
        </label>
      </div>
      <button
        class="btn-flat btn-large"
        @click="onAdd">
          <i
            class="material-icons"
          >
            add
          </i>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'AddT',
  props: [
    'addTData',
    'onAdd'
  ]
}
</script>

<style scoped>

</style>



