<template>
  <div>
    <div class="nav-wrapper">
      <form>
        <div class="input-field">
          <input
            id="search"
            v-model="searchDesc"
            placeholder="Szukaj opisu"
            type="search"
            required>
          <label class="label-icon" for="search"><i class="material-icons">search</i></label>
          <i
            @click="searchDesc = ''"
            class="material-icons">
            close</i>
        </div>
      </form>
    </div>

    <list-t
      :editId="editId"
      :editData="editData"
      :onCancel="onCancel"
      :onEdit="onEdit"
      :onEditSubmit="onEditSubmit"
      :sortedT="sortedT"
      :sortedP="sortedP"
      :onSelect="onSelect"
      :nDate="nDate"
      :loading="loading"
      @search-surname="searchSurname = $event"
    />
  </div>
</template>

<script>
import db from '@/db'
import ListT from './ListT'

export default {
  name: 'Give',
  data () {
    return {
      territories: [],
      publishers: [],
      editId: '',
      editData: {},
      searchDesc: '',
      searchSurname: '',
      nDate: new Date(),
      loading: true,
    }
  },
  components: {
    ListT,
  },
  computed: {
    sortedT () {
      return this.territories.slice().filter(t=>t.desc.toLowerCase().includes(this.searchDesc.toLowerCase())).sort((a, b) => {
        return a.number - b.number
      })
    },
    sortedP () {
      return this.publishers.slice().filter(p=>p.surname.toLowerCase().includes(this.searchSurname.toLowerCase()))
    }
    },
    created () {
      this.getT()
      this.getP()
    },
  methods: {
    getT () {
      db.collection('territories').where("free", "==", true).get().then(querySnapshot => {
        const territories = []
        querySnapshot.forEach(doc => {
          territories.push(doc.data())
        })
        this.territories = territories
        this.loading = false
        // M.toast({html: 'Pobrałem dane terenów'})
      }).catch(function(error) {
          M.toast({html: 'Błąd: ' + error})
          })
    },
    getP () {
      db.collection('publishers').get().then(querySnapshot => {
        const publishers = []
        querySnapshot.forEach(doc => {
          publishers.push(doc.data())
          this.publishers = publishers
          // M.toast({html: 'Pobrałem dane głosicieli'})
        })
      }).catch(function(error) {
        M.toast({html: 'Błąd: ' + error})
      })
    },
    onEdit (t) {
      this.editId = t.tid
      this.editData.number = t.number
      this.editData.desc = t.desc
      this.editData.tel = t.tel
      this.editData.given = t.given
      this.editData.recieved = t.recieved
      this.editData.free = t.free

    },
    onCancel () {
      this.editId = ''
      this.editData = {}
      this.searchSurname = ''
    },
    // p.has.add date tid desc ...
    onEditSubmit () {
      // if (!this.editData.has.length) this.editData.has = []
      // this.editData.has.push({
      //   date: this.nDate.getTime(),
      //   number: this.editData.number,
      //   desc: this.editData.desc,
      // })
      // db.collection('publishers')
      //   .where('pid', '==', this.editData.pid).get()
      //   .then(querySnapshot => {
      //     querySnapshot.forEach(doc => {
      //       doc.ref.set({
      //       pid: this.editData.pid,
      //       name: this.editData.name,
      //       surname: this.editData.surname,
      //       has: this.editData.has
      //       }).then(this.getP)
      //     })
      //   })
      this.editData.given.push({
        date: this.nDate.getTime(),
        pid: this.editData.pid,
        name: this.editData.name,
        surname: this.editData.surname,
        })
      db.collection('territories')
        .where('tid', '==', this.editId).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            doc.ref.set({
              tid: this.editId,
              number: this.editData.number,
              desc: this.editData.desc,
              tel: this.editData.tel,
              given: this.editData.given,
              recieved: this.editData.recieved,
              free: false,
            }).then(this.getT)
          })
          M.toast({html: 'Teren przydzielony'})
          this.onCancel()
        })

    },
    onSelect (p) {
      this.nDate = new Date()
      this.editData.pid = p.pid
      this.editData.name = p.name
      this.editData.surname = p.surname
      this.editData.has = p.has
    }
  }
}
</script>

<style scoped>

</style>



