<template>
  <div>
    <h3>Przydzielone tereny</h3>
    <div
      v-if="loading"
      class="progress grey lighten-5">
      <div class="indeterminate green"></div>
    </div>
    <div
      v-for="t in sortedT"
      :key="t.tid"
      class="collection"
    >
      <div
        v-if="editId === t.tid"
        class="collection-item list-item list-edit"
        :class="{ 'green lighten-5': editId === t.tid }"
      >
      <div>
          <span>
            {{t.number}}
          </span>
          <span>
            {{ (t.tel)? "☎":"" }}
          </span><br>
          {{t.desc}} <br>
          {{t.given[t.given.length-1].name}}
          {{t.given[t.given.length-1].surname}}
        </div>
        <label>Data opracowania:
          <input type="date" :value="nDate && nDate.toISOString().split('T')[0]"
            @input="nDate = $event.target.valueAsDate">
        </label>
        <div>
          <button
            class="btn-flat btn-large"
            @click="onEditSubmit()">
            <i
              class="material-icons"
            >
              check
            </i>
          </button>
          <button
            class="btn-flat btn-large"
            @click="onCancelHandler">
            <i
              class="material-icons"
            >
              cancel
            </i>
          </button>
        </div>
      </div>
      <div
        v-else
        class="collection-item list-item"
      >
        <div>
          <span>
            {{t.number}}
          </span>
          <span>
            {{ (t.tel)? "☎":"" }}
          </span>
        </div>
        <div>{{t.desc}}</div>
        <div>
          <button
            @click="onEdit(t)"
            class="btn-flat btn-small green lighten-5"
          >
            Zwróć
          </button>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ListT',
  data() {
    return {
      searching: ""
    }
  },
  props: [
    'editId',
    'editData',
    'onCancel',
    'onDelete',
    'onEdit',
    'onEditSubmit',
    'sortedT',
    'onSelect',
    'sortedP',
    'searchSurname',
    'nDate',
    'loading',
  ],
  methods: {
    onCancelHandler() {
      this.searching = ''
      this.onCancel()
    },
  },
}
</script>

<style scoped>
  .list-edit{
    align-items: flex-start;
  }
</style>



