<template>
  <div>
    <div v-if="loaded">
      <h3>{{name}} {{surname}}</h3>
      <p>pid: {{pid}}</p>
      <router-link :to="'/Publishers'">
        <button class="waves-effect waves-light btn-small green green">Wróć</button>
      </router-link>
    </div>
    <div v-else class="progress grey lighten-5">
      <div class="indeterminate green"></div>
    </div>
  </div>
</template>

<script>
import db from '@/db'

export default {
  name: 'PPage',
  data () {
    return {
      pid: "",
      name: "",
      surname: "",
      loaded: false
    }
  },
  beforeCreate () {
    db.collection('publishers')
      .where('pid', '==', this.$route.params.pid).get()
      .then(querySnapshot => {
        querySnapshot.forEach(doc => {
          const { pid, name, surname } = doc.data()
          this.pid = pid
          this.name = name
          this.surname = surname
        })
        this.loaded = true
      })
  }
}
</script>

<style scoped>

</style>



